@import url('https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400..700;1,400..700&family=Geologica:wght@100..900&display=swap');

body {
    margin: 0;
    padding: 0;
    background: black;
    color: rgb(142, 142, 147);
    user-select: none;

    font-family: "Geologica", sans-serif;
    font-optical-sizing: auto;
    font-style: normal;

}

img {
    box-sizing: border-box;
    height: auto;
    transition: opacity .75s;
}

#root {
    display: flex;
    justify-content: center;
}

.grid {
    display: grid;
    width: 100%;
    overflow: hidden;
    grid-template-columns: repeat(auto-fit, minmax(20%, 1fr));

    > img {
        opacity: .2;
        width: 100%;
        height: auto;
        object-fit: cover;

        &.bright {
            opacity: 1;
        }
    }

    &.loaded {
        > img {
            opacity: 1;
        }
    }
}

.notice {
    display: block;
    box-sizing: content-box;
    font-size: .95rem;
    text-align: center;
    background: rgb(28, 28, 30);
    padding: 1rem;
    margin: 2rem 3rem;
    border-radius: 1rem
}

.notice {
    h3 {
        margin: .75rem 0;
        padding: 0;
        color: rgb(229, 229, 234);
    }

    p {
        font-size: .9rem;
        margin: .5rem;
    }
}

.recommendations {
    margin-bottom: 6rem;

    .track {
        display: flex;
        position: relative;
        max-width: 400px;
        align-items: center;
        flex-direction: column;
        text-align: center;
        margin: 2rem;

        .track-actions {
            display: none;
            position: absolute;
            align-content: center;
            justify-content: center;
            flex-direction: column;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, .8);
            backdrop-filter: blur(4px);
            transition: background .5s ease, backdrop-filter 1.5s ease;
        }

        &.selected {
            .track-actions {
                display: flex;
            }
        }

        h3 {
            margin: .5rem;
            padding: 0;
            color: rgb(229, 229, 234);
        }

        p {
            margin: 0 0 1rem;
            padding: 0;
        }

        img {
            display: block;
            width: 90%;
            max-width: 320px;
            margin: 1rem;
        }
    }
}

.pause {
    opacity: .25;
    font-size: 10rem;
}

.controls {
    position: fixed;
    display: flex;
    align-items: center;
    box-sizing: border-box;
    bottom: 0;
    background: rgba(15, 15, 16, 0.75);
    backdrop-filter: blur(20px);
    width: 100%;
    padding: .88rem;

    button {
        font-size: .75rem;
    }

    .inner {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        max-width: 800px;
        margin: 0 auto;
    }

    @media all and (display-mode: standalone) {
        padding: .88rem .88rem 1.75rem;
    }
}

button {
    outline: none;
    border: none;
    border-radius: .25rem;
    padding: .75rem;
    background: transparent;
    color: rgb(242, 242, 247);
    font-weight: 700;
}

[data-tooltip] {
    position: relative;

    &:hover::before, &:focus::before, &:active::before {
        content: attr(data-tooltip);
        position: absolute;
        font-weight: 600;
        left: 50%;
        top: .4rem;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        transform: translateX(-50%);
        padding: .5rem;
        background: rgba(23, 23, 23, 0.47);
        backdrop-filter: blur(30px);
        color: rgb(142, 142, 147);
        border-radius: .25rem;
        font-size: .5rem;
    }
}

.btn-spotify {
    display: inline-block;
    background: #1db954;
    color: white;
    padding: .75rem 1rem;
    border-radius: 2rem;
    font-weight: 700;
    text-decoration: none;
    font-size: .9rem;
    text-transform: uppercase;
    align-items: center;
    justify-content: center;
    gap: .5rem;
    cursor: pointer;
    transition: background .25s;

    &:hover {
        background: #1ed760;
    }

    &:active {
        background: #1aa34a;
    }
}
